import {FC, useState} from "react"
import {Box, Button} from "@mui/material";

type PropsType = {
    totalUsers: number
    pageSize: number
    changePage: (n: number) => void
    currentPage: number
}

export const Paginator: FC<PropsType> = ({pageSize, totalUsers, changePage}) => {
    const pagesCount = Math.ceil(totalUsers / pageSize)
    const pages = []
    for (let i = 1; i < pagesCount; i++) {
        pages.push(i)
    }
    const portionCount = Math.ceil(pagesCount / 10)
    const [portionNumber, setPortionNumber] = useState(1)
    const leftPortionPageNumber = (portionNumber - 1) * 10 + 1
    let rightPortionPageNumber = portionNumber * 10
    // return <Pagination count={pagesCount}/>
    return (
        <Box>
            {portionNumber > 1 &&
                <Button sx={{padding: 0}} variant={'contained'}
                        onClick={() => setPortionNumber(portionNumber - 1)}>prev</Button>}
            {
                pages
                    .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                    .map((p) => {
                        return <Box sx={{padding:'5px',border:1,fontSize:'16px',cursor:'pointer',fontFamily:'Arial'}}
                                    component='span'
                                    key={p} onClick={() => changePage(p)}>
                            {p}</Box>
                    })
            }
            {portionCount > portionNumber &&
                <Button sx={{padding: 0}} variant={'contained'}
                        onClick={() => setPortionNumber(portionNumber + 1)}>NEXT</Button>}
        </Box>
    )
}

