import {ErrorMessage, Field, Form, Formik} from "formik";
import * as Yup from "yup";
import {Box, Button} from "@mui/material";
import React, { FC } from "react";
import {FilterType} from "../../store/reducers/users-reducer/users-reducer";

type PropsType = {
    filterChanged: (filter:FilterType) => void
}

export const UsersSearchForm:FC<PropsType> = ({filterChanged}) => {
    const handleSubmit = (values:FilterType) => {
        filterChanged(values)
    }
    return (
        <Formik
            initialValues={{term: '',friend:null}}
            validationSchema={
                Yup.object({
                    term: Yup.string(),
                })
            }
            onSubmit={(values:FilterType) => handleSubmit(values)}
        >
            <Form style={{height:'50px'}}>
                <Box sx={{display:'flex',alignItems:'center',justifyContent:'center'}}>
                    <Field id='term' name='term' type='text'/>
                    <Field name='friend' as='select' value='null'>
                        <option value='null'>All</option>
                        <option value='true'>Followed</option>
                        <option value='false'>Unfollowed</option>
                    </Field>
                    <Button sx={{p:'5px 15px',fontSize:'14px'}} variant='contained' type='submit'></Button>
                </Box>
                <ErrorMessage name={'term'} component={'div'}/>
            </Form>
        </Formik>
    )
}